.container-fluid {
  background-color: rgb(0, 0, 0);
}

.row .col-xl-5 {
  padding: 4.2vmin 1vmin;
}

.row .col-xl-7 {
  padding: 10vmin 1vmin 13%;
}

.row .col-xl-5 img {
  width: 75%;
}

.row .col-md-5 img {
  width: 60%;
}

.col-xl-7 h1 {
  color: rgb(255, 255, 255);
  padding: 1vmin 5vmin;
  text-align: start;
  font-size: 8.5vmin;
}

.col-xl-7 p {
  color: rgb(255, 255, 255);
  padding: 1vmin 5vmin;
  text-align: start;
}

.col-xl-7 .btn {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  border: 0.125rem solid rgb(0, 0, 0);
  margin-top: 2rem;
  padding: 2vmin 5vmin;
}

.col-xl-7 .btn:hover {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  border: 0.125rem solid rgb(255, 255, 255);
}

.contactLink{
	color: inherit;
	text-decoration: none;
}

.contactLink:hover{
	color: inherit;
}

.ColorChange .container-fluid {
  background-color: rgb(255, 255, 255);
  transition: 0.5s;
}

.ColorChange p {
  color: rgb(0, 0, 0);
}

.ColorChange h1 {
  color: rgb(0, 0, 0);
}

.ColorChange .btn {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  border: 0.125rem solid rgb(255, 255, 255);
}

.ColorChange .btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  border: 0.125rem solid rgb(0, 0, 0);
}
