.numberBox {
  position: absolute;
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 0.313rem;
  z-index: 2;
  margin-top: 4rem;
}

.timelineCard {
  position: relative;
  width: 50%;
  min-height: 7.5rem;
}

.cardPosition-left {
  left: 0;
  padding-right: 4.375rem;
  text-align: right;
}

.cardPosition-right {
  left: 50%;
  padding-left: 4.375rem;
  text-align: left;
}

.cardPosition-left .numberBox {
  right: 0;
  margin-right: -2rem;
}

.cardPosition-right .numberBox {
  left: 0;
  margin-left: -2.5rem;
}

.cardPosition-left .card {
  -webkit-clip-path: polygon(0% 0%, 90% 0, 100% 50%, 90% 100%, 0% 100%);
          clip-path: polygon(0% 0%, 90% 0, 100% 50%, 90% 100%, 0% 100%);
}

.cardPosition-right .card {
  -webkit-clip-path: polygon(10% 0, 100% 0%, 100% 100%, 10% 100%, 0% 50%);
          clip-path: polygon(10% 0, 100% 0%, 100% 100%, 10% 100%, 0% 50%);
}

