}

@media screen and (max-width: 768px) {
  .numberBox {
    margin-top: 8rem;
  }
}

@media screen and (max-width: 767px) {
  .timelineCard .card::before {
    content: ' ';
    height: 0;
    position: absolute;
    top: -0.063rem;
    width: 0;
    z-index: 1;
    border: medium solid rgb(255, 255, 255);
  }
  .timelineCard {
    width: 100%;
    padding-right: 0%;
    margin-top: 1.875rem;
  }
  .timelineCard:first-of-type {
    margin-top: 0%;
  }
  .cardPosition-right {
    left: 0%;
    padding-left: 0%;
  }
  .cardPosition-left {
    text-align: left;
  }
  .right {
    margin-left: 0%;
  }
  .cardPosition-left .card {
    -webkit-clip-path: none;
            clip-path: none;
  }
  .cardPosition-right .card {
    -webkit-clip-path: none;
            clip-path: none;
  }
  .cardPosition-left .card::before {
    left: 47.5%;
    top: -0.625rem;
    border: medium solid rgb(255, 255, 255);
    border-width: 0 0.625rem 0.625rem 0.625rem !important;
    border-color: transparent transparent rgb(0, 0, 0) transparent !important;
  }
  .cardPosition-right .card::before {
    left: 47.5%;
    top: -0.625rem;
    border: medium solid rgb(255, 255, 255);
    border-width: 0 0.625rem 0.625rem 0.625rem;
    border-color: transparent transparent rgb(255, 255, 255) transparent;
  }
  .card {
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    width: 100%;
    max-width: 100%;
    margin-top: var(--bs-gutter-y);
  }
  .numberBox {
    position: relative !important;
    margin: 1rem auto !important;
  }
  .numberBox h1 {
    padding-top: 20% !important;
  }
}

